<template>
  <div>
    <el-card style="height:auto;min-height:1000px">
      <el-form ref="AddNewDevices" :model="AddNewDevices" label-width="80px" style="width:100% margin:auto">
            <el-form-item label="">
                <el-input v-model="AddNewDevices.title"  style="width:25%"></el-input>
            </el-form-item>
            <el-form-item label="UDID">
                <el-input v-model="AddNewDevices.udid"  style="width:25%"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="AddNewDevices.name"  style="width:15%"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="AddNewDevices.brand"  style="width:15%"></el-input>
            </el-form-item>
            <el-form-item label="CPU">
                <el-input v-model="AddNewDevices.cpu"  style="width:15%"></el-input>
            </el-form-item>
            <!-- <el-form-item label="GPU">
                <el-input v-model="AddNewDevices.gpu"  style="width:15%"></el-input>
            </el-form-item> -->
            <el-form-item label="">
              <el-select v-model="AddNewDevices.platform" placeholder="">
                  <el-option label="Android" value="2"></el-option>
                  <el-option label="IOS" value="1"></el-option>
                </el-select>
            </el-form-item>   
            <el-form-item label="">
                <el-input v-model="AddNewDevices.system"  style="width:15%"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="AddNewDevices.resolution"  style="width:15%"></el-input>
            </el-form-item>
            <el-form-item label="">
                  <el-select v-model="AddNewDevices.screen" placeholder="">
                    <el-option label="" value="3"></el-option>
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="5"></el-option>
                    <el-option label="" value="4"></el-option>
                    <el-option label="" value="6"></el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="AddNewDevices.ram"  style="width:15%"></el-input>
            </el-form-item>                           
            <div style="text-align:center">
                <el-button type="primary"  @click="submitForm"></el-button>
                <el-button type="primary"  @click="rest"></el-button>
            </div>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      AddNewDevices: {
        title: "",
        udid: "",
        name: "",
        brand: "",
        cpu: "",
        gpu: "",
        system: "",
        resolution: "",
        screen: "",
        ram: "",
        platform: ""
      },
      isRouterAlive:true
    };
  },
  created() {
  },
  methods: {
    // 
    async submitForm() {
      const { data: res } = await this.$http.get("approval_equip_add", {
        params: this.AddNewDevices
      });
			if (res.meta.status !== 200) {
				if(res.meta.status == 401){
          this.$message.error("");
					return this.$router.push('./login')
				}
				return this.$message.error(res.meta.msg);
			}
      // 
      this.$message.success("");
    },
    rest(){
      this.AddNewDevices = {
        title: "",
        udid: "",
        name: "",
        brand: "",
        cpu: "",
        gpu: "",
        system: "",
        resolution: "",
        screen: "",
        ram: "",
        platform: ""
      }
    }
  }
};
</script>