<template>
	<div>
		<!--  -->
		<el-card style="height:auto;min-height:1000px">
			<!-- -->
			<div class="nav" style="margin-top: 15px;">
				<el-row :gutter="20">
					<el-col :span="18">
						<el-input
							placeholder=""
							v-model="queryInfo.query"
							class="input-with-select"
							clearable
							@clear="getDevicesList"
						>
							<el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
						</el-input>
					</el-col>
					<!-- <el-col :span="6">
						<div class="grid-content bg-purple"></div>
					</el-col>-->
				</el-row>
			</div>
			<!--  -->
			<!--  -->
			<el-table :data="DevicesList" style="width: 100%" border stripe>
				<!--  -->
				<el-table-column type="index" label="" width="50" align="center"></el-table-column>
				<!-- <el-table-column prop="mobile" label="" sortable align="center"></el-table-column>                -->
				<el-table-column prop="title" label="id" sortable align="center"></el-table-column>
				<el-table-column prop="name" label="" sortable align="center"></el-table-column>
				<el-table-column prop="brand" label="" sortable align="center"></el-table-column>
				<el-table-column prop="udid" label="udid" sortable align="center" ></el-table-column>
				<el-table-column prop="resolution" label="" sortable align="center" ></el-table-column>
				<el-table-column prop="system" label="" sortable align="center"></el-table-column>
				<el-table-column prop="cpu" label="CPU" sortable align="center"></el-table-column>
				<el-table-column prop="ram" label="" sortable align="center"></el-table-column>
				<el-table-column prop="platform" label="" sortable align="center"></el-table-column>
				<el-table-column  label="" sortable align="center">
					<template slot-scope="scope">
						<el-tag type="sucess" round v-if="scope.row.state===''" effect="plain"></el-tag>
						<el-tag type="info" round v-if="scope.row.state===''"  effect="plain"></el-tag>
						<el-tag type="warning" round v-if="scope.row.state===''" effect="plain"></el-tag>
						<el-tag type="danger" round v-if="scope.row.state===''" effect="plain"></el-tag>
					</template>
				</el-table-column>
				<el-table-column prop="user_name" label="" sortable align="center"></el-table-column>			
			</el-table>
			<!--  -->
			<!--  -->
			<el-pagination
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:current-page="queryInfo.pagenum"
				:page-sizes="[1, 2, 30, 120]"
				:page-size="queryInfo.pagesize"
				layout="total, sizes, prev, pager, next, jumper"
				:total="total"
			></el-pagination>
		</el-card>
	</div>
</template>


<script>
export default {
	// 
	data() {
        //
        // var checkdate = (rule, value, callback) => {
        //     if (!value) {
        //     return callback(new Error(''))
        // }};
		return {
			//
			queryInfo: {
				query: "",
				// 
				pagenum: 1,
				// 
                pagesize: 30,
                // 
                list_type:"IOS"
			},
			input: "",
			//
			userList: [],
			total: 0,
			DevicesList: [],
			DevicesData: [],
            formLabelWidth: "120px",
            // DateRules:{
            //     date:{ validator: checkdate, trigger: 'blur' }
			// }
        }
	},
	created() {
		//
		// this.getUserList();
		this.getDevicesList();
		// this.Supply();
        // this.Search();
	},
	methods: {
		// ,
		async getDevicesList() {
			// 
			const {data:res} = await this.$http.get("equip_list", {
				params: this.queryInfo
			});
			if (res.meta.status !== 200) {
				if(res.meta.status == 401){
					this.$message.error("");
					return this.$router.push('./login')
				}
				return this.$message.error(res.meta.msg);
			}
			// 
			this.DevicesList = res.data.dict.list;
			this.total = res.data.dict.total;
			console.log(this.DevicesList);
		},
		//pagesize
		handleSizeChange(newSize) {
			console.log(newSize);
			this.queryInfo.pagesize = newSize;
			this.getDevicesList();
		},
		//
		handleCurrentChange(newPage) {
			// console.log(newPage)
			this.queryInfo.pagenum = newPage;
			this.getDevicesList();
        },
        // id
        showSupplyDilog(DeviceId){
            this.AddSupplyDialog = true
            // console.log(DevicesId)
            this.supplyform.deviceid = DeviceId
        },
		//
        Search() {
            // console.log(this.queryInfo.query)
            this.getDevicesList()
        }
	},
};
</script>

<style lang="less" scoped>
.nav {
	margin-bottom: 10px;
}
.button {
	margin: 10px;
}
</style>>