<template>
    <div>
        <el-card style="height:auto;min-height:1000px">
            <!-- -->
			<div style="margin-top: 15px;">
				<el-row :gutter="20">
					<el-col :span="10">
						<el-input
							placeholder=""
							v-model="data.query"
							class="input-with-select"
							clearable
							@clear="getborrowlist"
						>
							<el-button slot="append" icon="el-icon-search" @click="search"></el-button>
						</el-input>
					</el-col>
                    <el-button  round type="danger" size="mini"  @click="RemindAll" style="float:right"></el-button>
                    <el-button  round type="primary" size="mini" style="float:right;margin-bottom:10px;margin-right:30px" @click="cancelLock"></el-button>
                    <el-button  round type="danger" size="mini" style="float:right;margin-bottom:10px;margin-right:20px" @click="Lock"></el-button>
				</el-row>
			</div>
            <el-table :data="borrowlist" border style="width: 100%" stripe>
                <el-table-column type="index" label="" width="80" sortable align="center"></el-table-column>
                <el-table-column  label="" sortable align="center">
                    <template slot-scope="scope">
                        <el-popover placement="right" width="400" trigger="click">
                               <!-- {{userinfo}}  -->
                            <el-table :data="userinfo">
                                <el-table-column prop="user_name" label=""></el-table-column>
                                <el-table-column prop="group" label=""></el-table-column>
                                <el-table-column prop="department" label=""></el-table-column>
                                <el-table-column prop="local" label=""></el-table-column>
                                <el-table-column prop="phone" label=""></el-table-column>
                            </el-table>   
                            <el-button slot="reference" @click="getuserinfo1(scope.row.user.user_id)"  type="text" >{{scope.row.user.user_name}}</el-button>
                        </el-popover>
                    </template> 
                </el-table-column>     
                <el-table-column prop="start_time" label="" sortable align="center"></el-table-column>
                <el-table-column prop="equipment.title" label="" sortable align="center"></el-table-column>
                <el-table-column prop="equipment.name" label="" sortable align="center"></el-table-column>
                <el-table-column prop="plan_time" label="" sortable align="center"></el-table-column>
                <el-table-column prop="reason" label="" sortable align="center"></el-table-column>
                <el-table-column prop="state" label="" sortable align="center"></el-table-column>
                <el-table-column label="" align="center">
                    <template slot-scope="scope">
						<!-- {{scope.row}} -->
                         <div style="display:flex;">
                           <el-button type="success" round v-if="scope.row.state===''" size="small" plain @click="MakeSureBorrow(scope.row.pk,true)" style="margin-left:15px" :disabled="isDisabled"></el-button>
                        <el-button type="danger" round v-if="scope.row.state===''" size="small" plain @click="MakeSureBorrow(scope.row.pk,false)" :disabled="isDisabled"></el-button>
                         </div>
                        <div style="display:flex">
                                <el-button type="success" round v-if="scope.row.state===''" size="small" plain @click="MakeSureBacked(scope.row.pk)" :disabled="isDisabled"></el-button>  
                            <el-button type="primary" round v-if="scope.row.state===''" size="small" plain @click="Remind(scope.row.pk)" :disabled="isDisabled"></el-button>
                        </div>
                        <div style="display:flex">
                                <el-button type="success" round v-if="scope.row.state===''" size="small" plain @click="MakeSureBacked(scope.row.pk)" :disabled="isDisabled"></el-button>  
                            <el-button type="primary" round v-if="scope.row.state===''" size="small" plain @click="Remind(scope.row.pk)" :disabled="isDisabled"></el-button>
                        </div>                        
					</template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>

<script>
export default {
	data() {
		return {
            borrowlist: [],
            data:{
                query:""
            },
            check:{
                id:"",
                ispass:""
            },
            userinfo:[{
                user_name:"",
                phone:"",
                group:'',
                department:"",
                local:''
            }],
            isDisabled:false,
            // userinfo:[]
		};
    },
    created(){
        this.getuserinfo()
        this.getborrowlist()
    },
    methods:{
        // 

       async getborrowlist(){
			const { data : res } = await this.$http.get("approval_list",{
                params:this.data
            });
			if (res.meta.status !== 200) {
				if(res.meta.status == 401){
                    this.$message.error("");
					return this.$router.push('./login')
				}
				return this.$message.error(res.meta.msg);
			}
            // 
            this.borrowlist = res.data.dict.list;
        }, 
        // &
        async Lock(){
            const {data:res} = await this.$http.get("lock_approval");
            if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg);
            }
            this.$message.success("");  
        },
        async cancelLock(){
            const {data:res} = await this.$http.get("unlock_approval");
            if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg);
            }
            this.$message.success("");
        },
        search(){
            this.getborrowlist()
        },
        // 
        async Remind(key){
            // var id=key
            this.timeDelay();
            var id ={"id":key}
            const {data:res} = await this.$http.get("tip_return_one",{
                params:id
            });
            if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
            }
            this.getborrowlist();
            this.$message.success("");             
        },            
        // 
        timeDelay(){
			const Time_count = 2
			if(!this.timer){
				this.count =Time_count;
				this.isDisabled =true
				this.timer = setInterval(()=>{
					if(this.count>0 && this.count <=Time_count){
						this.count--;
					}else{
						this.isDisabled = false
						clearInterval(this.timer)
						this.timer =null;
					}
				},1000)
			}
		},
        // 
        MakeSureBorrow(pk,i){
            this.$confirm("",'',{
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                center: true
                }).then( async () => {
                    this.check.id = pk;
                    this.check.ispass = i;
                    const {data:res} = await this.$http.get("approval",{
                        params:this.check
                    });
                    if (res.meta.status !== 200) {
                        return this.$message.error(res.meta.msg);
                    }
                    this.getborrowlist();
                    this.$message.success("");    
                 }).catch(() => {
                return this.$message({
                    type: 'info',
                    message: ''
                }); 
            });                                 
        },
        // 
        MakeSureBacked(key){
            this.$confirm("",'',{
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                center: true
                }).then( async () => {
                    var id ={"id":key}
                    const {data:res} = await this.$http.get("approval_do_return",{
                    params:id
                    });
                    if (res.meta.status !== 200) {
                        return this.$message.error(res.meta.msg);
                    }
                    this.getborrowlist();
                    this.$message.success(""); 
                }).catch(() => {
                return this.$message({
                    type: 'info',
                    message: ''
                });
            });  
        },
        // 
        async RemindAll(){
            const {data:res} = await this.$http.get("tip_return_all");
            if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
            }
            this.getborrowlist();
            this.$message.success("");            
        },
        // 
        async getuserinfo1(key){
            var id ={"id":key}
            const {data:res} = await this.$http.get("get_userinfo",{params:id})
            console.log(res)
            if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
            }
            // this.userinfo =  res.data.dict.equipment.fields
            this.userinfo[0].user_name = res.data.dict.u_name
            this.userinfo[0].phone = res.data.dict.phone
            this.userinfo[0].group = res.data.dict.group
            this.userinfo[0].department = res.data.dict.department
            this.userinfo[0].local = res.data.dict.local
           
        },
        getuserinfo(){
            //
            // var demo = [
            //     {
            //     user:{
            //         user_name:"",
            //         user_id:123
            //     }, 
            //     equipment:{
            //         title:9527,
            //         name:'10'
            //     },
            //     start_time: "2016-05-03",
            //     state:"",
            //     plan_time:'2016-05-03',
            //     },
            //     {
            //     user:{user_name:""}, 
            //     equipment:{
            //         title:9528,
            //         name:'9'
            //     },
            //     start_time: "2016-05-03",
            //     state:"",
            //     plan_time:'2016-05-03',
            //     },
            // ];
            // // this.borrowlist = demo
        }
    }
};
</script>