<template>
	<div>
		<el-card  style="height:auto;min-height:1000px">
			<!-- -->
			<div class="nav" style="margin-top: 15px;">
				<el-row :gutter="20">
					<el-col :span="18">
						<el-input
							placeholder=""
							v-model="queryInfo.query"
							class="input-with-select"
							clearable
							@clear="getDevicesList"
						>
							<el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
						</el-input>
					</el-col>
					<!-- <el-col :span="6">
						<div class="grid-content bg-purple"></div>
					</el-col>-->
				</el-row>
			</div>
			<!--  -->
			<!--  -->
			<el-table :data="DevicesList" style="width: 100%" border stripe>
				<!--  -->
				<el-table-column type="index" label="" width="50" align="center"></el-table-column>
				<el-table-column prop="title" label="" sortable align="center"></el-table-column>
				<el-table-column prop="name" label="" sortable align="center"></el-table-column>
				<el-table-column prop="brand" label="" sortable align="center" ></el-table-column>
				<el-table-column prop="udid" label="udid" sortable align="center" ></el-table-column>
				<el-table-column prop="resolution" label="" sortable align="center" ></el-table-column>
				<el-table-column prop="system" label="" sortable align="center"></el-table-column>
				<el-table-column prop="cpu" label="CPU" sortable align="center"></el-table-column>
				<el-table-column prop="gpu" label="GPU" sortable align="center" ></el-table-column>
				<el-table-column prop="ram" label="" sortable align="center"></el-table-column>
				<el-table-column prop="platform" label="" sortable align="center" ></el-table-column>
				<el-table-column  label="" align="center">
					<template slot-scope="scope">
						<el-tag type="sucess" round v-if="scope.row.state===''" effect="plain"></el-tag>
						<el-tag type="info" round v-if="scope.row.state===''"  effect="plain"></el-tag>
						<el-tag type="warning" round v-if="scope.row.state===''" effect="plain"></el-tag>
						<el-tag type="danger" round v-if="scope.row.state===''" effect="plain"></el-tag>
					</template></el-table-column>
				<el-table-column prop="user_name" label=""  sortable align="center"></el-table-column>
				<el-table-column label="" width="125" align="center">
					<template slot-scope="scope">
						<!-- {{scope.row}} -->
						<el-button type="primary" round v-if="scope.row.state===''" @click="showSupplyDilog1(scope.row.id)"></el-button>
						<el-button type="danger" round v-if="scope.row.state!==''" @click="showSupplyDilog2(scope.row.id)"></el-button>
					</template>
				</el-table-column>
			</el-table>			
			<!--  -->
			<!--  -->
			<el-pagination
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:current-page="queryInfo.pagenum"
				:page-sizes="[1, 30, 60, 90]"
				:page-size="queryInfo.pagesize"
				layout="total, sizes, prev, pager, next, jumper"
				:total="total"
			></el-pagination>
		</el-card>
		<!--  -->
		<el-dialog title="" :visible.sync="AddSupplyDialog1">
			<!--  -->
			<el-form :model="supplyform" ref="supplyform">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="supplyform.email"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="date">
					<el-col :span="11">
						<el-date-picker
							type="date"
							value-format="yyyy-MM-dd"
							placeholder=""
							v-model="supplyform.rtime"
							style="width: 100%;"
						></el-date-picker>
					</el-col>
				</el-form-item>
			</el-form>
			<!--  -->
			<div slot="footer" class="dialog-footer">
				<el-button @click="AddSupplyDialog1 = false"> </el-button>
				<el-button type="primary" @click="Supply" :disabled="isDisabled"> </el-button>
			</div>
			<!--  -->
		</el-dialog>
		<el-dialog title="" :visible.sync="AddSupplyDialog2">
			<!--  -->
			<el-form :model="supplyform" ref="supplyform">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="supplyform.email"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="supplyform.reason"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="date">
					<el-col :span="11">
						<el-date-picker
							type="date"
							value-format="yyyy-MM-dd"
							placeholder=""
							v-model="supplyform.rtime"
							style="width: 100%;"
						></el-date-picker>
					</el-col>
				</el-form-item>
			</el-form>
			<!--  -->
			<div slot="footer" class="dialog-footer">
				<el-button @click="AddSupplyDialog2 = false"> </el-button>
				<el-button type="primary" @click="mergeSupply"  :disabled="isDisabled"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>


<script>
export default {
	// 
	data() {
        //
        // var checkdate = (rule, value, callback) => {
        //     if (!value) {
        //     return callback(new Error(''))
        // }};
		return {
			//
			queryInfo: {
				query: "",
				// 
				pagenum: 1,
				// 
                pagesize: 30,
                // 
                list_type:"IOS"
			},
			input: "",
			//
			userList: [],
			total: 0,
			DevicesList: [],
			DevicesData: [],
			// 
			AddSupplyDialog1: false,
			AddSupplyDialog2: false,
			//
			supplyform: {
				deviceid: "",
				reason: "",
				rtime: "",
				email: ""
			},
            formLabelWidth: "120px",
            // DateRules:{
            //     date:{ validator: checkdate, trigger: 'blur' }
			// }
			isDisabled:false
        }
	},
	created() {
		//
		// this.getUserList();
		this.getDevicesList();
		// this.Supply();
        // this.Search();
	},
	methods: {
		// ,
		async getDevicesList() {
			// 
			const {data:res} = await this.$http.get("equip_list", {
				params: this.queryInfo
			});
			console.log(res)
			if (res.meta.status !== 200) {
				return this.$message.error("");
            }
			// 
			this.DevicesList = res.data.dict.list;
			this.total = res.data.dict.total;
		},
		//pagesize
		handleSizeChange(newSize) {
			console.log(newSize);
			this.queryInfo.pagesize = newSize;
			this.getDevicesList();
		},
		//
		handleCurrentChange(newPage) {
			// console.log(newPage)
			this.queryInfo.pagenum = newPage;
			this.getDevicesList();
        },
		// id
		showSupplyDilog1(DeviceId) {
			this.AddSupplyDialog1 = true;
			this.supplyform.reason = ''
			// console.log(DevicesId)
			this.supplyform.deviceid = DeviceId;
			this.supplyform.email = this.$cookies.get("email");
		},
		// 
		timeDelay(){
			const Time_count = 5
			if(!this.timer){
				this.count =Time_count;
				this.isDisabled =true
				this.timer = setInterval(()=>{
					if(this.count>0 && this.count <=Time_count){
						this.count--;
					}else{
						this.isDisabled = false
						clearInterval(this.timer)
						this.timer =null;
					}
				},1000)
			}
		},
		// id
		showSupplyDilog2(DeviceId) {
			this.AddSupplyDialog2 = true;
			// console.log(DevicesId)
			this.supplyform.deviceid = DeviceId;
			this.supplyform.email = this.$cookies.get("email");
		},
		//
		async Supply() {
			this.timeDelay();
			// 
			// this.supplyform.userid = window.sessionStorage.getItem('userid')
            const {data:res} = await this.$http.get("borrow_apply",{
				params: this.supplyform
            });
            if(res.meta.status !==200){
                return this.$message.error('');   
            }
            //
            this.AddSupplyDialog1 = false
            // 
            this.getDevicesList()
            //
            this.$message.success('')
            
		},
		// 
		async mergeSupply() {
			this.timeDelay();
			const { data: res } = await this.$http.get("borrow_apply", {
				params: this.supplyform
			});
			if (res.meta.status !== 200) {
				return this.$message.error("");
			}
			//
			this.AddSupplyDialog2 = false;
			// 
			this.getDevicesList();
			//
			this.$message.success("");
		},
		//
        Search() {
            // console.log(this.queryInfo.query)
            this.getDevicesList()
        }
        //
        // closeAddSupply(){
        //     this.$refs.form.resetFields()
        // }
	},
};
</script>

<style lang="less" scoped>
.nav {
	margin-bottom: 10px;
}
.button {
	margin: 10px;
}
</style>>