</template>
<script>
export default {
  data(){
return {  
      pageInfo: {
        pagenum: 1,
        pagesize: 5, 
        currentData:[],
      },
      data:{
        Result:false,
        Version:'1.0.0',
        Date:'2020-10-20',
        Platform:'Android',
        DevicesNum:100,
        ProblemDevices:5,
        PassRate:null,
        DeviceRequire:'8407.0',
        PassRateHistory:null,
        error_type:null,
      },
      Brand:null,
      MainCard:[{
        title1:'CPU[%]',
        title2:'',
        title3:'',
        CurrentAvg:13,
        TypeAvg:13,
        HistoryAvg:136,
        icon:"iconfont icon-cpu"
      },
      {
        title1:'[KB]',
        title2:'',
        title3:'',
        CurrentAvg:30,
        TypeAvg:136,
        HistoryAvg:136,
        icon:"iconfont icon-dbfx"
      },
      {
        title1:'[MB]',
        title2:'',
        title3:'',
        CurrentAvg:16,
        TypeAvg:136,
        HistoryAvg:136,
        icon:"iconfont icon-RectangleCopy"
      },
      {
        title1:'[FPS]',
        title2:'',
        title3:'',
        CurrentAvg:100,
        TypeAvg:136,
        HistoryAvg:136,
        icon:"iconfont icon-xingneng"
      },
      // {
      //   title1:'GPU[%]',
      //   title2:'',
      //   title3:'',
      //   CurrentAvg:'',
      //   TypeAvg:'',
      //   HistoryAvg:'',
      // },
      ],
      trend:{
        version:["v1.0.0","v1.1.0","v1.2.0","v1.3.0","v1.4.0","v1.5.0"],
        value:[50,100,100,99,98,100]
      },
      TestStep:[{setpName:'',result:true},{setpName:'',result:true},{setpName:'',result:true},{setpName:'',result:true},{setpName:'',result:false},]

    }

  },
  created(){
    this.getTestInfo();

  },
  computed:{
  },
  methods:{
     // --
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.TestStep.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },
    DoEchart_PassRatePie(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var rate=this.data.PassRate
      let option = {
        legend:{
         // orient:'vertical',
          left:'right',
          bottom:10,
          data:['','']
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        color:["#8da2e9",'#c5cce2'],
        series: [
          {
            type: 'pie',
            radius: ['40%', '75%'],
            center:['50%','60%'],
            data: [{
                value: rate, 
                name: '',
                label:{
                   // color:"red",
                    show:true,
                    formatter:'{d}%'
                }},
                {value: 100-rate,
                name:'',
                label:{
                  show:false,
                }
                },
            ],
            label:{
                show:true,
                position:"center",
                fontSize:"15",
                fontWeight:"bold"
            }
          }
        ]}
      this.BrokenLine.setOption(option)
    },
    DoEchart_ProblemPie(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let problemdata= [
          {value: this.data.error_type.InstallFail, name: ''},
          {value: this.data.error_type.LaunchFail, name: ''},
          {value: this.data.error_type.ForceExit, name: ''},
          {value: this.data.error_type.Black, name: ''},
          {value: this.data.error_type.FuncError, name: ''},
          {value: this.data.error_type.NoRep, name: ''},
          {value: this.data.error_type.UiError, name: 'UI'},
          {value: this.data.error_type.Other, name: ''},
      ]
      let Noproblemdata=[{value:0,name:""}]
      let devices=[
          {value: this.Brand.DevicesNum[0], name: this.Brand.brand[0]},
          {value: this.Brand.DevicesNum[1], name: this.Brand.brand[1]},
          {value: this.Brand.DevicesNum[2], name: this.Brand.brand[2]},
          {value: this.Brand.DevicesNum[3], name: this.Brand.brand[3]},
          {value: this.Brand.DevicesNum[4], name: this.Brand.brand[4]},
          {value: this.Brand.DevicesNum[5], name: this.Brand.brand[5]},
      ]

      let data=[]
      let Color= []
      let Name=''
      let LegendData=[]
      if(key=="ProblemPie"){
        Color = ["#c9b5b6 ","#cec8c8 ","#a09a9a ","#916a6a","#74c5d3 ","#5b919b  ","#7dc1a7 ",'#b4c187 ']
        Name = ''
        LegendData = ['', '', '', '', '', '', 'UI', '','']
        if(this.data.Result==true){
          data = Noproblemdata
          Color = ["#2cea39"]
        }else{
          data= problemdata
        }
      }
      if(key=="devicesPie"){
          Color = ["#840344 ","#3730a5 ","#215682 ","#217e82","#267c3a ","#343d22  ","#bcb045 ",'#bf3fa9 ']
          LegendData = this.Brand.brand
          Name = ''
          data = devices
      }

      let option = {
          tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b}: {c} ({d}%)'
          },
          color:Color,
          toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                restore: {show: true},
                saveAsImage: {show: true}
            }
          },
          legend: {
              orient: 'vertical',
              right: 10,
              data: LegendData
          },
          series: [
              {
                  name:Name,
                  type: 'pie',
                  radius:'65%',
                  center:['50%','50%'],
                  selectMode:'single',
                  emphasis: {
                      itemsStyle: {
                          shadowBlur:10,
                          shadowOffsetx:0,
                          shadowColor: 'rgba(0,0,0,0.5)'
                      }
                  },
                  data:data
              }
          ]
      };

      this.BrokenLine.setOption(option)
    },
    DoEchart(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let option = {
        tooltip: {
            trigger: 'axis',
        },
        legend: {
            data: ['' ]
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                boundaryGap:false,
                data:this.trend.arrayVersion
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: {
          name: "",
          type: 'line',
          data:this.trend.arrayPassRate ,
          markPoint: {
              data: [
                  {type: 'max', name: ''},
                  {type: 'min', name: ''}
              ]
          },
          markLine:{
            data:[{type:"average",name:''}]
          }
        }
      };
       this.BrokenLine.setOption(option)
    },

    async getTestInfo(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('testinfo_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.data.Result=res.data.dict.res.Summery.Result
      this.data.Date=res.data.dict.res.Summery.Date
      this.data.DevicesRequire=res.data.dict.res.Summery.DeviceRequire
      this.data.PassRate=res.data.dict.res.Summery.PassRate
      this.data.ProblemDevices=res.data.dict.res.Summery.ProblemDevices
      this.data.DevicesNum=res.data.dict.res.Summery.DevicesNum
      this.data.Platform=res.data.dict.res.Summery.Platform
      this.data.Version=res.data.dict.res.Summery.Version
      this.data.PassRateHistory=res.data.dict.res.Summery.PassRateHistory
      this.data.error_type = res.data.dict.res.error_type

      this.TestStep = res.data.dict.res.TestStep
      this.trend = res.data.dict.res.Trend
      this.MainCard[0].CurrentAvg = res.data.dict.res.cpu.Current
      this.MainCard[0].HistoryAvg = res.data.dict.res.cpu.HistoryAvg
      this.MainCard[0].TypeAvg = res.data.dict.res.cpu.TypeAvg
      this.MainCard[1].CurrentAvg = res.data.dict.res.data.Current
      this.MainCard[1].HistoryAvg = res.data.dict.res.data.HistoryAvg
      this.MainCard[1].TypeAvg = res.data.dict.res.data.TypeAvg
      this.MainCard[3].CurrentAvg = res.data.dict.res.fps.Current
      this.MainCard[3].HistoryAvg = res.data.dict.res.fps.HistoryAvg
      this.MainCard[3].TypeAvg = res.data.dict.res.fps.TypeAvg
      this.MainCard[2].CurrentAvg = res.data.dict.res.memory.Current
      this.MainCard[2].HistoryAvg = res.data.dict.res.memory.HistoryAvg
      this.MainCard[2].TypeAvg = res.data.dict.res.memory.TypeAvg

      this.Brand =  res.data.dict.res.brand




    setTimeout(()=>{
      this.DoEchart_PassRatePie("PassRatePie");
      this.DoEchart_ProblemPie("ProblemPie");
      this.DoEchart_ProblemPie("devicesPie");
      this.DoEchart("TrendChart")
    },200)
      this.setCurrentPageData()
    }


  }
  
}
</script>


