<template>
  <div id="app">
    <div id="nav">
      <el-container style="background-color:white">
        <el-header style="display:flex">
          <span style="float:left"></span>
        </el-header>
        <el-main>
          <el-card>
            <!--  -->
            <div style="display:inline-block">   
              <div style="float:left;margin-top:0px">   
                <el-select v-model="uploadAgent" placeholder="" style="margin-top:-20px;margin-left:0px" @change="selectUrl">
                  <el-option v-for="item in optionAgent" :key="item" :label="item" :value="item"> </el-option>
                </el-select>
              </div>
              <!---->
              <div style="margin:-7px 0px 0 10px;float:left">
                <el-upload 
                  ref="upload"
                  drag
                  :action="url"
                  multiple
                  :with-credentials="true"
                  :on-success="upFile"
                  :on-remove="handleRemove"
                  :auto-upload="false"
                  accept=".*">
                  <i class="el-icon-upload" style="margin-top:10px"></i>
                  <div class="el-upload__text"><em></em></div>
                </el-upload>
              </div>
              <div style="float:right;margin-top:85px;margin-left:30px">
                <el-button type="primary"  @click="submitUpload"></el-button>    
              </div>          
            </div>
            <el-divider></el-divider>
            <!--  -->
            <template>
              <el-table ref="reportTable" :data="tableData" style="width: 100%;cursor:pointer" highlight-current-row    @selection-change="getServalReport">>
                <!-- <el-table-column type="index" label=""></el-table-column> -->
                <el-table-column type="selection"></el-table-column>
                <el-table-column  label="" width="auto"> 
                  <div slot-scope="scope" class="project">
                      <span style="margin-left:10px ;font-weight:bold;color:blue" @click="Report(scope.row)">{{scope.row.GameName}}</span>
                  </div>
                </el-table-column>
                <el-table-column prop="Version" sortable label=""  width="auto"></el-table-column>
                <el-table-column prop="name" sortable label=""  width="auto"></el-table-column>
                <el-table-column  prop="testDate" sortable label="" width="auto"></el-table-column>
                 <el-table-column  prop="testDate" sortable label="(min)" width="300"></el-table-column>
                <el-table-column  prop="FPS_avg" label="Fps" > </el-table-column>
                <el-table-column prop="CPU_avg" label="Cpu[%]" > </el-table-column>
                <el-table-column prop="Memory_avg" label="Memory[MB]" ></el-table-column>  
                <el-table-column prop="Net_avg" label="[kb]" ></el-table-column>                
                <el-table-column prop="Ele_avg" label="[mAh]" ></el-table-column>    
                <el-table-column prop="Tem_avg" label="[]"></el-table-column>                                          
              </el-table>
            </template>
            <div style="margin-top:40px;margin-bottom:40px; text-align:center" >
               <el-button type="primary" @click="submitFormQuick"></el-button>
               <el-button type="success" @click="handleDataInfo"></el-button>
               <el-button type="warning" @click="Delete"></el-button>
            </div>   
          </el-card>  
        </el-main>
        <!--  -->
        <el-dialog :visible.sync="dialogVisable" title="" class="dialog2" @close="DeleteData">
          <!-- logo -->
          <span style="float:left;margin:auto 15px auto 30px"></span>
          <el-upload 
            ref="upload2"
            :action="url2"
            :with-credentials="true"
            :on-success="upFile"
            :on-remove="handleRemove"
            :data="upData"
            :auto-upload="false"
            :file-list="fileList"
            accept=".png">
              <el-button size="small" type="primary" style="float:left"> </el-button>
            </el-upload>
            <el-form :model="setGameData" label-width="100px">
              <el-form-item label="">
                <el-input  v-model="setGameData.GameName" ></el-input>
              </el-form-item>
               <span style="float:left;margin:auto 15px auto 30px"></span>
               <el-select v-model="setGameData.Platform" placeholder="" style="margin-left:0px">
                  <el-option v-for="item in optionAgent" :key="item" :label="item" :value="item"> </el-option>
              </el-select>
              <el-form-item label="" >
                <el-input  v-model="setGameData.Version" ></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="setGameData.Engine"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="setGameData.GameType"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="setGameData.Company"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-date-picker v-model="setGameData.TestDate" type="date" format="yyyy  MM  dd " value-format="yyyy-MM-dd"  placeholder="">
                </el-date-picker>
                <!-- <el-input v-model="setGameData.TestDate"></el-input> -->
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="setGameData.TestTime"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm">(logo)</el-button>     
                <el-button type="primary" @click="submitForm1"></el-button>          
                <el-button @click="Cancle"></el-button>
              </el-form-item>
            </el-form>
        </el-dialog>
      </el-container>    
    </div>
  </div>
</template>
<script>
export default {
  data(){
return {
        //androidios
        uploadAgent:'',
        optionAgent:['Android','ios'
        ],
        fileList:[],
        // 
        url:this.$http.defaults.baseURL,
        url2:this.$http.defaults.baseURL+"/confirm_devices/",
        // 
        // 
        optionData:[],

        // 
        setGameData:{
        },
        //  
        tableData: [],       
        Selectionid:[], // 
        Selectiongameid:[],
        Selectionversionid:[],
        dialogVisable:false,
        dialogVisable1:false,
      }

  },
  created(){
    this.getRecentProject();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    testbutton(){
    
    },
    // 
    async getRecentProject(){
      const {data:res} = await this.$http.get("init_manage_list");
      console.log(res)
      this.tableData = res.data.list
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
    },

    //
    handleDataInfo(){
      if(this.Selectionid.length){
        this.setGameData.id = this.Selectionid;
        this.setGameData.GameId = this.Selectiongameid;
        this.setGameData.VersionId = this.Selectionversionid;
        this.dialogVisable = true
        console.log(this.setGameData)
      }else{
        this.$message.info("")
      }
    },
    // 
    submitFormQuick(){
        if(this.Selectionid.length){
        this.$confirm("",'',{
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
          }).then( async () => {
              var id = JSON.stringify({id:this.Selectionid})
              const {data:res} = await this.$http.get("confirm_devices_quick",{
              params:id
              });
              if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg);
              }
              this.getRecentProject();
              this.$message.success(res.meta.msg); 
          }).catch(() => {
              return this.$message({
                type: 'error',
                message: ""
          });
       });  
      }else{
        return this.$message.error("")
      }
    },
    // 
    Delete(){
      if(this.Selectionid.length){
        this.$confirm("",'',{
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
          }).then( async () => {
              var id = JSON.stringify({id:this.Selectionid})
              const {data:res} = await this.$http.get("device_delete",{
              params:id
              });
              if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg);
              }
              this.getRecentProject();
              this.$message.success(res.meta.msg); 
          }).catch(() => {
              return this.$message({
                type: 'error',
                message: ""
          });
       });  
      }else{
        return this.$message.error("")
      }
      
    },
    // 
    // getReport(val){     
    //   console.log(val)
    // },
    // id
    getServalReport(val){
      console.log(val)
      this.Selectionid=[]
      this.Selectiongameid=[]
      this.Selectionversionid = []
      for(var i =0;i<val.length;i++){
        this.Selectionid[i] = val[i].id;
        this.Selectiongameid[i] = val[i].GameId;
        this.Selectionversionid[i] = val[i].VersionId;
      }
      console.log(this.Selectionid)
    },
    // 
    Report(val){
      this.setGameData = val
      this.setGameData.id= val.id
      this.setGameData.GameId=val.GameId
      this.setGameData.VersionId=val.VersionId
      this.dialogVisable=true
    },
    
    // url
    selectUrl(){
      this.url = this.url+'/upload_file_'+this.uploadAgent+'/'
    },
     // 
    submitUpload(){
      this.$refs.upload.submit();
    },
    // 
    submitForm(){
      // console.log(this.fileList)
      // this.$http.post("confirm_devices/",this.setGameData);//
      this.$refs.upload2.submit();
        
    },
    //
    async submitForm1(){
      const {data:res} = await this.$http.post("confirm_devices/",this.setGameData);
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
      this.dialogVisable = false
      this.getRecentProject();
      return this.$message.success(res.meta.msg);
    },
    // {
    Cancle(){
      this.dialogVisable=false
      this.getRecentProject();
    },
    // dialog
    DeleteData(){
      this.setGameData={
        },
      this.url=this.$http.defaults.baseURL,
      this.uploadAgent=''
    },
    //
    // add(form) {
    //   this.$refs[form].validate(async valid => {
    //     if (valid) {
    //     // 
    //       this.$refs.upload.submit()
    //     } else {
    //       return false;
    //     }
    //   }); 
    // },
    // 
    upFile(res) {
      console.log(res)
      if (res.meta.status == 200) {
        // 
        this.dialogVisable1 = false
        this.dialogVisable = false
        this.getRecentProject();
        return this.$message.success(res.meta.msg);
      } else {
        this.$message.warning(res.meta.msg);
        let _this = this;
        setTimeout(function() {
          _this.$refs.upload.clearFiles();
        }, 1000);
      }
    },
    //  
    handleRemove(file, fileList) {
      console.log(file,fileList)
      this.$message.warning('');
    },
  }
}
</script>

<style>
  .el-upload-dragger{
    height: 135px;
  }
  .el-header, .el-footer {
    background-color: rgb(23, 24, 22);
    color: rgb(255, 255, 254);
    text-align: center;
    line-height: 60px;
    width: 100%;
  }
  .el-main {
    background-color:snow;
    width: 100%;
  }
  .el-card{
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100%;
  }
  .el-select{
    margin:5px 5px 10px 10px;
  }
  .project{
    display: flex;
    align-items: center;
  }
  .dialog{
    height: auto;
    display: flex;
    flex-direction: column;
    margin:0 !important;
    position: absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-80%);
    animation:none !important
    /* max-height: calc(100% -5000px);
    max-width: calc(100% -30px); */
  }
  .dialog2{
   width: center;
   
  }


