<template>
    <div>
        <el-card style="height:auto;min-height:1000px">
            <el-table :data="borrowlist" border style="width: 100%" stripe>
                <el-table-column type="index" label="" width="80" sortable align="center"></el-table-column>
                <el-table-column prop="start_time" label="" sortable align="center"></el-table-column>
                <el-table-column prop="equipment.title" label="id" sortable align="center"></el-table-column>
                <el-table-column prop="equipment.name" label="" sortable align="center"></el-table-column>
                <el-table-column prop="plan_time" label="" sortable align="center"></el-table-column>
                <el-table-column prop="state" label="" sortable align="center"></el-table-column>
                <el-table-column  label="" align="center">
					<template slot-scope="scope">
					<el-button  type="primary" size="mini" round @click="showSupplyDilog2(scope.row.equipment.id)" ></el-button>
					</template>
				</el-table-column>
            </el-table>
        </el-card>
        <el-dialog title="" :visible.sync="AddSupplyDialog2">
			<!--  -->
			<el-form :model="supplyform" ref="supplyform">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="supplyform.email"  disabled></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="supplyform.reason"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="date">
					<el-col :span="11">
						<el-date-picker
							type="date"
							value-format="yyyy-MM-dd"
							placeholder=""
							v-model="supplyform.rtime"
							style="width: 100%;"
						></el-date-picker>
					</el-col>
				</el-form-item>
			</el-form>
			<!--  -->
			<div slot="footer" class="dialog-footer">
				<el-button @click="AddSupplyDialog2 = false"> </el-button>
				<el-button type="primary" @click="mergeSupply" :disabled="isDisabled"> </el-button>
			</div>
		</el-dialog>

    </div>
</template>

<script>
export default {
	data() {
		return {
            borrowlist: [],
            AddSupplyDialog2:false,
            isDisabled:false,
            formLabelWidth:"120px",
            //
			supplyform: {
				deviceid: "",
				reason: "",
				rtime: "",
				email: ""
			},
		};
    },
    created(){
        this.getuserinfo()
    },
    methods:{
        // 
       async getuserinfo(){
			const { data: res } = await this.$http.get("borrow_list_user");
			if (res.meta.status !== 200) {
				return this.$message.error("");
            }
            // 
			this.borrowlist = res.data.dict.list;
			console.log(res);
		},
        // id
		showSupplyDilog2(id) {
			this.supplyform.rtime = '';
			this.AddSupplyDialog2 = true;
			// console.log(DevicesId)
			this.supplyform.deviceid = id;
			this.supplyform.email = this.$cookies.get("email");
        },
        // 
		async mergeSupply() {
			console.log(this.supplyform)
			this.timeDelay();
			const { data: res } = await this.$http.get("borrow_apply", {
				params: this.supplyform
			});
			if(this.supplyform.rtime==''){
				return this.$message.error("")
			}
			if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
			}
			//
			this.AddSupplyDialog2 = false;
			// 
            this.getuserinfo()
			//
			this.$message.success("");
        },
        // 
		timeDelay(){
			const Time_count = 5
			if(!this.timer){
				this.count =Time_count;
				this.isDisabled =true
				this.timer = setInterval(()=>{
					if(this.count>0 && this.count <=Time_count){
						this.count--;
					}else{
						this.isDisabled = false
						clearInterval(this.timer)
						this.timer =null;
					}
				},1000)
			}
		},
    }
};
</script>