</template>
<script>
export default {
  name: "Summary",
  data(){
    return {
      // 
      MainCard:[{
          title:"FPS()",
          desc:"",
          data:0,
          sugdata:25,
          unit:'',
          sug:["1.CPUTop","2.GCTopGC"
          ,"3.","4."
          ]
        },{
          title:"ReservedMono(MB)",
          desc:"Mono",
          data:0,
          sugdata:80,
          unit:'MB',
          sug:["1.UnityAssetBundle Mono",
          "2.MonoReservedMonoUPRReservedMono",
          "3.Mono",
          "4.AssetBundle"
          ]
        },{
          title:"Texture(MB)",//
          desc:"",
          data:0,
          sugdata:50,
          unit:'MB',
          sug:["Mipmap",
          "1.Game",
          "2.(Alpha) Alpha UnityRGBA",
          "3.",
          "4.Mipmap",
          "5.Use Crunch CompressionCrunchUnity"]
        },{
          title:"Mesh(MB)",//
          desc:"",
          data:0,
          sugdata:20,
          unit:'MB',
          istrue:false,
          sug:["1. ",
          "2. Optimize Mesh"]
        },{
          title:"Animation(MB)",
          desc:"",
          data:0,
          sugdata:15,
          unit:'MB',
          istrue:false,
          sug:["1. ",
          "2. ",
          "3. ",
          "4. "]
        },{
          title:"Audio(MB)",
          desc:"",
          data:0,
          sugdata:15,
          unit:'MB',
          istrue:false,
          sug:["1.",
          "2.Load Type Streaming",
          "3..mp3.ogg.wav .aiff"]
        },{
          title:"DrawCall()",
          desc:"Draw Call",
          data:0,
          sugdata:250,
          unit:'',
          istrue:false,
          sug:["1.UI DrawCall15-40UI DrawCall",
          "2.DrawCall",
          ]
        },{
          title:"Tris()",
          desc:"",
          data:0,
          sugdata:200000,
          unit:'',
          istrue:false,
          sug:["1.",
          "2.Game Object",
          "3."
          ]
        }
      ],
      // id
      id:null,
      isCollapse: false,
      testdata:{
        fps:0,
        max_reserved_mono:0,
        max_texture:0,
        max_mesh:0,
        max_animation:0,
        max_audio:0,
        max_draw_call:0,
        max_tris:0
      },
      SummarytData:{},
      CPUSummary:{
        frameIdx:[],
        frameTime:[],
        renderingTime:[],
        scriptTime:[],
        physicsTime:[],
      },
      testimglist:['http://10.0.10.246/icon/.png','http://10.0.10.246/icon/.png','http://10.0.10.246/icon/.png','http://10.0.10.246/icon/3.png'],
      testData1:[],
      color_Object:{
        green:'green',
        red:'red',
      },
      // 
      model : {
        name:'',
        callNum:'',
        selfsum:'',
        selfperFrame:'',
        totalsum:'',
        totalperframe:'',
      },
      FuncTop30test : [{
        name:'1',
        callNum:'1',
        selfsum:'1',
        selfperFrame:'1',
        totalsum:'1',
        totalperframe:'1',
      }],
      FuncTop30:[],
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//Echart
      testdata3:'',
        //
      csvdata:'',
      datamodel:{
        CPUtotal:[],
        Render:[],
        Audio:[],
        Other:[]
      }
    }
  },
  created(){
    this.id = window.localStorage.getItem("id");
    this.getSummary();
    this.getCPU();
    //this.setimglist();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    // 
    async getSummary(){
      const{data:res} = await this.$http.get("get_u3d_overview",{
        params:this.id})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.SummarytData = res.data.dict.overview

      //console.log(this.SummarytData)
      this.HandleData()
      this.select_color();
     
      this.FuncTop30.length = res.data.dict.model_dict.main_self.name.length
      for(let i=0;i<res.data.dict.model_dict.main_self.name.length;i++){
        this.FuncTop30[i] = Object.assign({},this.model) //
        this.FuncTop30[i].name = res.data.dict.model_dict.main_self.name[i]
        this.FuncTop30[i].callNum = res.data.dict.model_dict.main_self.callNum[i]
        this.FuncTop30[i].selfsum = res.data.dict.model_dict.main_self.selfsum[i]
        this.FuncTop30[i].selfperFrame = res.data.dict.model_dict.main_self.selfperFrame[i]
        this.FuncTop30[i].totalsum = res.data.dict.model_dict.main_self.totalsum[i]
        this.FuncTop30[i].totalperframe = res.data.dict.model_dict.main_self.totalperFrame[i]
      }

    },
    // CPU
    async getCPU(){
      const value={
        id:this.id,
        type:3,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.testimglist = res.data.dict.img;
      window.localStorage.setItem("imglist",JSON.stringify(this.testimglist));

      this.CPUSummary.frameIdx = res.data.dict.category_mainThread_frame.frameIdx;
      window.localStorage.setItem("frameIdx",JSON.stringify(this.CPUSummary.frameIdx));
      this.CPUSummary.frameTime = res.data.dict.category_mainThread_frame.frameTime;
      this.CPUSummary.renderingTime = res.data.dict.category_mainThread_frame.Render;
      this.CPUSummary.scriptTime = res.data.dict.category_mainThread_frame.Scripts;
      this.CPUSummary.physicsTime = res.data.dict.category_mainThread_frame.Physics;
      setTimeout(()=>{
        this.DoEchart()
      },500)
    },
    //
    setimglist(){
      window.localStorage.setItem("imglist",JSON.stringify(this.testimglist));
    },

    HandleData(){
      this.testData1.length=this.MainCard.length
      this.testData1[0]=this.SummarytData.fps.toFixed(2)
      this.testData1[1]=this.SummarytData.max_reserved_mono.toFixed(2)
      this.testData1[2]=this.SummarytData.max_texture.toFixed(2)
      this.testData1[3]=this.SummarytData.max_mesh.toFixed(2)
      this.testData1[4]=this.SummarytData.max_animation.toFixed(2)
      this.testData1[5]=this.SummarytData.max_audio.toFixed(2)
      this.testData1[6]=this.SummarytData.max_draw_call
      this.testData1[7]=this.SummarytData.max_tris
    },
    select_color(){
      for(let i=0;i<this.MainCard.length;i++){
        this.MainCard[i].data = this.testData1[i]
        if(i==0){
          if(this.MainCard[i].data>this.MainCard[i].sugdata){
            this.MainCard[i].istrue = true
          }else{
            this.MainCard[i].istrue = false
          }
        }else{
          if(this.MainCard[i].data<this.MainCard[i].sugdata){
            this.MainCard[i].istrue = true
          }else{
            this.MainCard[i].istrue = false
          }
        }
      }
      //console.log(this.MainCard)
    },
    //
    DoEchart(){
      var ElementID = document.getElementById("CPU_table")
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var data1=[],data2=[],data3=[],data4=[]
      for(let i=0;i<this.CPUSummary.frameIdx.length;i++){
        data4.push(this.CPUSummary.frameTime[i].toFixed(2))
        data1.push(this.CPUSummary.renderingTime[i].toFixed(2))
        data2.push(this.CPUSummary.scriptTime[i].toFixed(2))
        data3.push(this.CPUSummary.physicsTime[i].toFixed(2))
      }
      var seriesData = []
      seriesData[0] = {
        name: "frameTime",
        type: 'line',
        data:data4 ,
        markPoint: {
            data: [
                {type: 'max', name: ''},
                {type: 'min', name: ''}
            ]
        },
      }
      seriesData[1] = {
        name: "renderingTime",
        type: 'line',
        data: data1,
        markPoint: {
            data: [
                {type: 'max', name: ''},
                {type: 'min', name: ''}
            ]
        },
      }
      seriesData[2] = {
        name: "scriptTime",
        type: 'line',
        data: data2,
        markPoint: {
            data: [
                {type: 'max', name: ''},
                {type: 'min', name: ''}
            ]
        },
      }
      seriesData[3] = {
        name: "physicsTime",
        type: 'line',
        data:data3,
        markPoint: {
            data: [
                {type: 'max', name: ''},
                {type: 'min', name: ''}
            ]
        },
      }
      let option = {
        title: {
            text: 'CPU(ms)',
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
            showDelay:0,//
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"    
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data: ['frameTime', 'renderingTime','scriptTime','physicsTime']
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.CPUSummary.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    }
  }
}
</script>


