<template>
  <div>
    <!--  -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Ad_Summary' }"></el-breadcrumb-item>
        <el-breadcrumb-item>GPU</el-breadcrumb-item>
      </el-breadcrumb> 
      <div style="height:auto;min-height:1000px">
          <el-table :data="problemData"  style="width: 100%" stripe>
              <!-- <el-table-column type="index" label="" align="center" width="300"></el-table-column> -->
              <el-table-column prop="installFail" label="" align="center"></el-table-column>
              <el-table-column prop="lauchFail" label="" align="center"></el-table-column>
              <el-table-column prop="forceExit" label="" align="center"></el-table-column>
              <el-table-column prop="functionError" label="" align="center"></el-table-column>
              <el-table-column prop="blackScreen" label="" align="center"></el-table-column>
              <el-table-column prop="noResponse" label="" align="center"></el-table-column>
              <el-table-column prop="UIError" label="UI" align="center"></el-table-column>
              <el-table-column prop="others" label="" align="center"></el-table-column>
          </el-table>
          <div style="display:flex;margin:50px 0 0 10px">
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="AllChange"></el-checkbox>
            <el-checkbox-group v-model="checkedProblem" @change="handleCheckedChange" style="margin-left:25px">
                <el-checkbox v-for="item in problem" :label="item" :key="item">{{item}}</el-checkbox>
            </el-checkbox-group>
          </div>

          <div  v-for="(item,index) in ArrayProblemData" :key="index" style="margin-top:30px;height:300px;display:flex;padding:10px;border-bottom:1px solid #eee">
            <div  style="width:45%;border-right:2px solid #eee"><img  v-image-preview style="width:360px;cursor:pointer;margin:50px 0 0 150px"   :src="homeIP+item.DevicePath+'/'+item.ScreenShot" /></div>
            <div style="padding:30px;margin-left:80px;color:grey;min-width:200px">
              <div style="margin-bottom:20px"><router-link :to="{path:'/AdDevice_detail',query:{Deviceid:item.id}}" style=" text-decoration:none;color:#0070f0" >   {{item.deviceNum}}</router-link> </div>
              <div style="margin-bottom:20px"><span>    {{item.FailType}}</span> </div>
              <div style="margin-bottom:20px"><span >  {{item.Step}}</span> </div>
              <div style="margin-bottom:20px"><span >  {{item.detail}}</span> </div>
              <div style="margin-bottom:20px"><span >   {{item.Time}}</span> </div>
              <div><a :href="homeIP+item.DevicePath+'/DeviceLog.log'" target="_blank" style="text-decoration:none;color:#0070f0">    </a> </div>
            </div>
          </div>

      </div>
  </div>     
</template> 
<script>
  const ProblemOptions = ['', '', '', '','', '', 'UI', ''];
export default {
  data(){
    return { 
      homeIP:'http://10.0.10.246/', 
      checkAll: true,
      isIndeterminate: false,
      problem: ProblemOptions,
      checkedProblem:ProblemOptions,

      problemData:[
        {
          installFail:0,//
          lauchFail:0,//
          forceExit:0,//
          blackScreen:0,//
          functionError:0,//
          noResponse:1,//
          UIError:0,//UI
          others:0,
        }
      ],

      errorData:null,

      ArrayProblemData:[
        // {
        // devicesNum:'OPPO A83',
        // id:111,
        // FailType:'',
        // ErrorStep:'',
        // Errortime:'2020-10-22 15:12:33',
        // Info:"",
        // DiviceLogPath:"http://10.0.10.246/report/RE0/20200919/Device0/device.log",
        // ErrorImgPath:"http://10.0.10.246/report/RE0/20200919/Device0/error.jpg",
        // }
      ]

    }
  },
  created(){
  this.getProblemInfo()


  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    AllChange(val) {
      console.log(this.errorData)
      if(val==false){
        this.ArrayProblemData= []
      }
      if(val==true){
        this.ArrayProblemData= []
        this.ArrayProblemData = this.ArrayProblemData.concat(this.errorData.InstallFail).concat(this.errorData.LaunchFail)
        .concat(this.errorData.ForceExit).concat(this.errorData.Black).concat(this.errorData.FuncError).concat(this.errorData.NoRep)
        .concat(this.errorData.UiError).concat(this.errorData.Other)
      }

      this.checkedProblem = val ? ProblemOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedChange(value) {

      this.ArrayProblemData= []
      if(value.length==8){
        this.ArrayProblemData = this.ArrayProblemData.concat(this.errorData.InstallFail).concat(this.errorData.LaunchFail)
        .concat(this.errorData.ForceExit).concat(this.errorData.Black).concat(this.errorData.FuncError).concat(this.errorData.NoRep)
        .concat(this.errorData.UiError).concat(this.errorData.Other)
      }

      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.InstallFail)
      }
      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.LaunchFail)
      }
      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.ForceExit)
      }
      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.Black)
      }
      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.FuncError)
      }
      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.NoRep)
      }
      if(value.indexOf('UI')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.UiError)
      }
      if(value.indexOf('')>-1){
        this.ArrayProblemData=this.ArrayProblemData.concat(this.errorData.Other)
      }

      let checkedCount = value.length;
      this.checkAll = checkedCount === this.problem.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.problem.length;
      
    },
    async getProblemInfo(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('allProblem_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.errorData = res.data.dict.res

      this.problemData[0].installFail=this.errorData.InstallFail.length
      this.problemData[0].lauchFail=this.errorData.LaunchFail.length
      this.problemData[0].forceExit=this.errorData.ForceExit.length
      this.problemData[0].blackScreen=this.errorData.Black.length
      this.problemData[0].functionError=this.errorData.FuncError.length
      this.problemData[0].noResponse=this.errorData.NoRep.length
      this.problemData[0].UIError=this.errorData.UiError.length
      this.problemData[0].others=this.errorData.Other.length
      this.ArrayProblemData = this.ArrayProblemData.concat(this.errorData.InstallFail).concat(this.errorData.LaunchFail)
      .concat(this.errorData.ForceExit).concat(this.errorData.Black).concat(this.errorData.FuncError).concat(this.errorData.NoRep)
      .concat(this.errorData.UiError).concat(this.errorData.Other)
      console.log(this.homeIP+this.ArrayProblemData[0].DevicePath)
    }
    
  }
}
</script>


