  .el-main {
    background-color:snow;
    width: 100%;
  }
  .el-aside {
	background-color: #333744;
  }
  .el-menu {
      border-right: none;
  }
  .imgHeader {
			width: 170px;
			margin-left: -18px;
			margin-top: 0px;
      float: left;
  }
  .toggle-button {
	background-color: #4a5064;
	font-size: 10px;
	line-height: 20px;
	text-align: center;
