<template>
	<el-container class="home_container">
		<!--  -->
		<el-container>
			<!--  -->
			<el-aside width="180px">
				<!--  -->
				<el-menu
					background-color="black"
					text-color="#fff"
					active-text-color="#409EFF"
					unique-opened
					:collapse="isCollapse"
					:collapse-transition="false"
					router
				>
					<!--  -->
					<el-submenu :index="devices+'1'">
						<!--  -->
						<template slot="title">
							<!--  -->
							<i class="el-icon-mobile"></i>
							<!--  -->
							<span></span>
						</template>
						<!--  -->
						<!--  -->
						<el-menu-item index="admindevices"></el-menu-item>
						<el-menu-item index="admindevices_ios">
							<template slot="title">
								<i class="el-icon-apple"></i>
								<span>ios</span>
							</template>
						</el-menu-item>
						<el-menu-item index="admindevices_android">
							<template slot="title">
								<i class="el-icon-mobile-phone"></i>
								<span>Android</span>
							</template>
						</el-menu-item>
					</el-submenu>
					<el-submenu index="' '">
						<!--  -->
						<template slot="title">
							<!--  -->
							<i class="el-icon-setting"></i>
							<!--  -->
							<span></span>
						</template>
						<!--  -->
						<el-menu-item index="checked">
							<template slot="title">
								<i class="el-icon-menu"></i>
								<span></span>
							</template>
						</el-menu-item>
						<el-menu-item index="addnew">
							<template slot="title">
								<i class="el-icon-menu"></i>
								<span></span>
							</template>
						</el-menu-item>	
						<el-menu-item @click="SelectOutType">
							<i class="el-icon-menu"></i>
							<span></span>
						</el-menu-item>				
					</el-submenu>
				</el-menu>
			</el-aside>
			<!--  -->
			<el-main>
				<!--  -->
				<router-view></router-view>
			</el-main>
		</el-container>
		<el-dialog title="" :visible.sync="outDialog" >
			<div style="margin-left:30%">
			<el-button type="primary" @click="outputInfo('')"></el-button>
			<el-button type="primary"  @click="outputInfo(2)"></el-button>
			<el-button type="primary"  @click="outputInfo(1)">ios</el-button>
			</div>
		</el-dialog>
	</el-container>
</template>

<script>
export default {
	data() {
		return {
			//
			menulist: [],
			iconsObj: {
				"125": "iconfont icon-user",
				"103": "iconfont icon-tijikongjian",
				"101": "iconfont icon-shangpin",
				"102": "iconfont icon-danju",
				"145": "iconfont icon-baobiao"
			},
			//
			isCollapse: false,
            devices: [],
            // 
            UserInfo:{
                email:"",
                dep:"",
                seat:""
			},
            showuserinfoDialog: false, 
            formLabelWidth:"100px",
            userid:'',
			firstlogin: false,
			DevicesList: [],
			queryInfo: {
                type: "",
			},
			list:[],
			outDialog:false,
		};
	},
	created() {
		//this.getDevicesList()
	},
	methods: {
		logout() {
			// token
			window.sessionStorage.clear();
			// 
			this.$router.push("/login");
		},
		//
		toggleCollapse() {
			this.isCollapse = !this.isCollapse;
		},

		// 
		async getDevicesList() {
			// 
			const { data: res } = await this.$http.get("all_equip_info",{params: this.queryInfo});
			if (res.meta.status !== 200) {
				return this.$message.error("");
			}
			this.DevicesList = res.data.dict;
			this.list = this.DevicesList.list
			return this.DevicesList;
		},
		// 
		SelectOutType(){
			this.outDialog = true;
		},
		// 
		outputInfo(type){
			this.DevicesList=[];
			this.queryInfo.type = type;
			let name ;
			if(type==1){
				name = "ios"
			}else if(type==2){
				name = "Android"
			}else{
				name=""
			}
			require.ensure([], async() => {
				const { export_json_to_excel } = require('../../../vendors/Export2Excel');
				const { data: res } = await this.$http.get("all_equip_info",{params: this.queryInfo});
				if (res.meta.status !== 200) {
					return this.$message.error("");
				}
				this.DevicesList = res.data.dict;
				this.list = this.DevicesList.list
				//console.log(this.DevicesList)
				var DevicesTotal = " :  "+(String)(this.DevicesList.count_num);
				var LendTotal = ":  "+(String)(this.DevicesList.lend_num);
				var Stock = ":  "+(String)(this.DevicesList.stock_num);
				var a = parseFloat(this.DevicesList.lend_num)
				var b = parseFloat(this.DevicesList.count_num)
				var borrowrate = ":  "+(String)((a/b).toFixed(2)*100+"%")
				const tHeader = ["","","","","","",DevicesTotal,LendTotal,Stock,borrowrate];//
				const filterVal = ["title","brand","name","ram","state","user_name"] //
				const list1 =this.list
				const data = this.formatJson(filterVal, list1);
				export_json_to_excel(tHeader, data, ''+name+'');
			})	
		},
		formatJson(filterVal, jsonData) {
			return jsonData.map(v => filterVal.map(j => v[j]))
		}
	}
};
</script>

<style lang="less" scoped>
.home_container {
	height: 100%;
}
.el-header {
	background-color: #373d41;
	display: flex;
	justify-content: space-between;
	padding-left: 0px;
	align-items: center;
	color: #fff;
	font-size: 18px;
	font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
	> div {
		display: flex;
		align-items: center;
		span {
			padding-left: 0px;
			margin-left: -12px;
		}
		img {
			width: 170px;
			margin-left: -10px;
			margin-top: 10px;
		}
	}
}
.el-aside {
	background-color: black;
	.el-menu {
		border-right: none;
	}
}
.el-main {
	background-color: #eaedf1;
}
.iconfont {
	margin-right: 10px;
}
.toggle-button {
	background-color: #4a5064;
	font-size: 10px;
	line-height: 20px;
	text-align: center;
	color: white;
	cursor: pointer;
}
</style>