<template>
	<el-container class="home_container">
		<!--  -->
		<el-container>
			<!--  -->
			<el-aside width='180px'>
				<!--  -->
				<el-menu
					background-color="black"
					text-color="#fff"
					active-text-color="#409EFF"
					unique-opened
					:collapse="isCollapse"
					:collapse-transition="false"
					router
				>
					<!--  -->
					<el-submenu :index="devices+''">
						<!--  -->
						<template slot="title">
							<!--  -->
							<i class="el-icon-mobile"></i>
							<!--  -->
							<span></span>
						</template>
						<!--  -->
						<!--  -->
						<el-menu-item index="devices"></el-menu-item>
						<el-menu-item index="devices_ios">
							<template slot="title">
								<i class="el-icon-apple"></i>
								<span>ios</span>
							</template>
						</el-menu-item>
						<el-menu-item index="devices_android">
							<template slot="title">
								<i class="el-icon-mobile-phone"></i>
								<span>Android</span>
							</template>
						</el-menu-item>
					</el-submenu>
					<el-menu-item index="user">
						<i class="el-icon-menu"></i>
						<span slot="title"></span>
					</el-menu-item>
					<el-menu-item @click="userinfo">
						<i class="el-icon-menu"></i>
						<span  ></span>
					</el-menu-item>
				</el-menu>
			</el-aside>
			<!--  -->
			<el-main>
				<!--  -->
				<router-view></router-view>
			</el-main>
		</el-container>
        <!--  -->
		<el-dialog title="" :visible.sync="showuserinfoDialog" >
			<!--  -->
			<el-form  :rules="rules" ref="UserinfoRef" :model="UserInfo">
				<el-form-item label="" :label-width="formLabelWidth" prop="email">
					<el-input v-model="UserInfo.email" style="width:70%"></el-input>
					<el-button type="success" id="getcode" @click="getEmailCode" :disabled="disabled=!show"> 
						<span v-if="show"></span>
						<span v-if="!show" class="count">{{count}} s</span>
					</el-button>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="emailcode">
					<el-input v-model="UserInfo.emailcode" maxlength="6"  show-word-limit style="width:150px;"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="group">
					<el-input v-model="UserInfo.group" ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="program">
					<el-input v-model="UserInfo.program" ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="workid">
					<el-input v-model="UserInfo.workid" ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="phone">
					<el-input v-model="UserInfo.phone" ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="local">
					<el-input v-model="UserInfo.local" ></el-input>
				</el-form-item>
			</el-form>                      
			<!--  -->
			<div slot="footer" class="dialog-footer">
				<el-button @click="showuserinfoDialog = false"> </el-button>
				<el-button type="primary" @click="setuserinfo"> </el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="showuserinfoDialogmerge" modal :show-close=false :close-on-click-modal=false :close-on-press-escape=false>
			<!--  -->
			<el-form  :rules="rules" ref="UserinfoRef" :model="UserInfo">
				<el-form-item label="" :label-width="formLabelWidth" prop="email">
					<el-input v-model="UserInfo.email" style="width:70%" placeholder=''></el-input>
					<el-button type="success" id="getcode" @click="getEmailCode" :disabled="disabled=!show"> 
						<span v-if="show"></span>
						<span v-if="!show" class="count">{{count}} s</span>
					</el-button>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="emailcode">
					<el-input v-model="UserInfo.emailcode" maxlength="6"  show-word-limit style="width:150px;"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="u_name">
					<el-input v-model="UserInfo.u_name" maxlength="6"  placeholder='jira' ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="group">
					<el-input v-model="UserInfo.group" placeholder='' ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="program">
					<el-input v-model="UserInfo.program" placeholder=''></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="workid">
					<el-input v-model="UserInfo.workid" placeholder=''></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="phone">
					<el-input v-model="UserInfo.phone" placeholder='' ></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="local">
					<el-input v-model="UserInfo.local" placeholder=''></el-input>
				</el-form-item>
			</el-form>                       
			<!--  -->
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="setuserinfo"> </el-button>
			</div>
		</el-dialog>		
	</el-container>
</template>

<script>
export default {
	data() {
		// 
		var checkPhone = (rule, value, callback) => {
				const phoneReg = /^1[3|4|5|6|7|8][0-9]{9}$/
				if (!value) {
				return callback(new Error(''))
				}
				setTimeout(() => {
					if (!Number.isInteger(+value)) {
						callback(new Error(''))
					} else {
						if (phoneReg.test(value)) {
							callback()
						} else {
							callback(new Error(''))
						}
					}
				}, 100)
		}
		var checkEmail = (rule, value, callback) => {
				const mailReg = /^([a-zA-Z0-9_-])+@(qiyi)+(.[a-zA-Z0-9_-])+/
				if (!value) {
					return callback(new Error(''))
				}
				setTimeout(() => {
					if (mailReg.test(value)) {
						callback()
					} else {
					callback(new Error(''))
					}
				}, 100)
		}
		return {
			//
			menulist: [],
			iconsObj: {
				"125": "iconfont icon-user",
				"103": "iconfont icon-tijikongjian",
				"101": "iconfont icon-shangpin",
				"102": "iconfont icon-danju",
				"145": "iconfont icon-baobiao"
			},
			//
			isCollapse: false,
            devices: [],
            // :,
            UserInfo:{
				u_name:'',
				email:"",
				emailcode:"",
                group:"",
				program:'',
				local:"",
				workid:'',
				phone:'',

			},
            showuserinfoDialog: false, 
            formLabelWidth:"100px",
            userid:'',
			showuserinfoDialogmerge: false, 
			//
			show:true,
			count:"",
			timer:null,
			rules:{
				email:[
					{ required: true,validator:checkEmail, trigger: 'blur' },
				],
				emailcode:[
					{ required: true, message:"",trigger: 'blur' }
				],
				u_name:[
					{required:true,message:"jira",trigger:'blur'}
				],
				group:[
					{required:true,message:"",trigger:'blur'}
				],
				program:[
					{required:true,message:"",trigger:'blur'}
				],
				local:[
					{required:true,message:"",trigger:'blur'}
				],
				workid:[
					{required:true,message:"",trigger:'blur'}
				],
				phone:[
					{required:true,validator:checkPhone ,trigger:'blur'}
				]
			}
		}
	},
	created() {
        // 
        this.isfirst()
		this.getuserinfo()
	

	},
	methods: {
		switchData(){
        var url = "http://10.0.10.246:8093/client_report"
        window.open(url)
		},
		logout() {
			// token
			window.sessionStorage.clear();
			// 
			this.$router.push("/login");
		},
		//,
		// async getMenulist() {
		// 	const { data: res } = await this.$http.get("menus");
		// 	if (res.meta.status !== 200) return this.$message.error(res.meta.msg);
		// 	this.menulist = res.data;
		// 	console.log(res);
		// },
		//
		toggleCollapse() {
			this.isCollapse = !this.isCollapse;
        },
        //
        userinfo(){
			this.UserInfo.emailcode = ""
			this.showuserinfoDialog = true
        },
        getuserinfo(){
			// this.UserInfo.email = window.sessionStorage.getItem("email")
			this.UserInfo.email = this.$cookies.get("email")
            this.UserInfo.group = window.localStorage.getItem("group")
			this.UserInfo.local = window.localStorage.getItem("local")
			this.UserInfo.program = window.localStorage.getItem("dep")
			this.UserInfo.workid = window.localStorage.getItem("job_number")
			this.UserInfo.phone = window.localStorage.getItem("phone")
			if(this.UserInfo.email =="null"){
				this.UserInfo.email = ''
				this.UserInfo.group =''
				this.UserInfo.local =''
				this.UserInfo.program = ''
				this.UserInfo.workid = ''
				this.UserInfo.phone = ''
			}
		},
		//
		async getEmailCode(){
			const Time_count = 60	
			var email={email:this.UserInfo.email}
			const {data:res} = await this.$http.get('get_emailcode',{ 
				params:email 
			})
			if(res.meta.status ==200){
				//token 
				// window.sessionStorage.removeItem("token");
				// window.sessionStorage.setItem("token",res.data.dict.token);
				if(!this.timer){
					this.count =Time_count;
					this.show =false
					this.timer = setInterval(()=>{
						if(this.count>0 && this.count <=Time_count){
							this.count--;
						}else{
							this.show = true
							clearInterval(this.timer)
							this.timer =null;
						}
					},1000)
				}
				return this.$message.success("")
			}else{
				return this.$message.error(res.meta.msg)
			}
		},
        // test1(){
        //     console.log(this.UserInfo.dep)
        // },
        //
        setuserinfo(){
			// 
			this.$refs.UserinfoRef.validate(async valid =>{ 
				if(!valid) return;
				const {data:res} = await this.$http.get('userinfo_up',{ 
					params:this.UserInfo
				})
				if(res.meta.status !==200){
					return this.$message.error(res.meta.msg);   
				}
				// 
				this.showuserinfoDialog = false
				this.showuserinfoDialogmerge = false
				this.$message.success(res.meta.msg)
				// 
				this.$cookies.set("email",res.data.dict.email,"7d")
				window.localStorage.setItem("email",this.UserInfo.email)
				window.localStorage.setItem("group",this.UserInfo.group)
				window.localStorage.setItem("local",this.UserInfo.local)
				window.localStorage.setItem("program",this.UserInfo.program)
				window.localStorage.setItem("workid",this.UserInfo.workid)
				window.localStorage.setItem("phone",this.UserInfo.phone)
				this.Userinfo.emailcode = ''
			});
        },
        // 
        isfirst(){
            // var test = false
			// this.firstlogin = test

			if(!this.$cookies.get("email") ||this.$cookies.get("email")=="null"|| window.localStorage.getItem("email")=="null"){
				this.showuserinfoDialogmerge = true
			}
        }
	}
};
</script>

<style lang="less" scoped>
.home_container {
	height: 100%;
}
.el-aside {
	background-color: black;
	.el-menu {
		border-right: none;
	}
}
.el-main {
	background-color: snow;
	height: 100%;
}
.iconfont {
	margin-right: 10px;
}

#getcode{
	float: right;
}
</style>